// Import dependencies
import { html, render, LitElement } from 'lit';
import { customElement, query, property } from 'lit/decorators.js';
import { createRef, ref } from 'lit/directives/ref.js';

const { isEmpty } = _uo.utility;

// Translatable strings
const strings = UncannyAutomator._core.i18n.components.itemLoop;

// eslint-disable-next-line require-jsdoc
@customElement( 'uap-builder-loop-add-token-loop' )
export class AutomatorBuilderLoopAddTokenLoop extends LitElement {
	@property( { type: String } ) accessor size;

	@property( { type: String } ) accessor color = 'secondary';

	// Create a reference to the dialog that doesn't exist yet
	$setTokenDialog = createRef();

	// Returns the component that has the fields of this loop filter
	// We'll use it to retrieve the values
	@query( 'uap-builder-item-legacy-form' ) accessor $optionsForm;

	// eslint-disable-next-line require-jsdoc
	render() {
		return html`
			<uo-button
				size=${ this.size }
				color=${ this.color }
				@click=${ this.handleAddTokenLoop }
			>
				${ strings.tokenLoop }
			</uo-button>
		`;
	}

	/**
	 * Handles adding a loop too the recipe
	 */
	handleAddTokenLoop() {
		// Select the token
		this.createEditFieldsDialog();
	}

	/**
	 * Creates the dialog that lets the user edit the fields of the loop
	 */
	createEditFieldsDialog() {
		// Create the node that we'll use to render the dialog
		// This node will be added as a direct child of the body node.
		// We can't add it inside the component created on this class because of z-index issues
		const $wrapper = document.createElement( 'div' );
		document.body.append( $wrapper );

		render(
			html`
				<uap-builder-item-loop-edit-dialog type="token"></uap-builder-item-loop-edit-dialog>
			`,
			$wrapper
		);
	}
}
